"use client";
import { useQuery, keepPreviousData } from "@tanstack/react-query";
import axios from "axios";
import { useState } from "react";

type Emoji = {
  name: string;
  category: string;
  htmlCode: string[];
};

export default function EmojiCatalog() {
  const [page, setPage] = useState(1);

  const { data, isLoading, error } = useQuery({
    queryKey: ["emoji", page],
    queryFn: async () => {
      const res = await axios.get("http://localhost:8000/emoji?page=1&page_size=20");
      return res.data;
    },
    placeholderData: keepPreviousData, 
  });

  if (isLoading) return <p className="p-4">Loading...</p>;
  if (error || !data) return <p className="p-4 text-red-500">Error loading emojis</p>;

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">Emoji Catalog</h1>

      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        {data.items?.map((emoji: Emoji, idx: number) => (
          <div key={idx} className="border rounded-xl p-4 text-center shadow">
            <p className="text-4xl">{emoji.htmlCode?.[0]}</p>
            <p className="font-bold">{emoji.name}</p>
            <p className="text-sm text-gray-500">{emoji.category}</p>
          </div>
        ))}
      </div>

      {/* Пагинация */}
      <div className="flex justify-center gap-4 mt-6">
        <button
          onClick={() => setPage((p) => Math.max(p - 1, 1))}
          disabled={page === 1}
          className="px-4 py-2 bg-gray-200 rounded disabled:opacity-50"
        >
          Prev
        </button>
        <span>
          Page {page} of {data.total_pages}
        </span>
        <button
          onClick={() => setPage((p) => (p < data.total_pages ? p + 1 : p))}
          disabled={page === data.total_pages}
          className="px-4 py-2 bg-gray-200 rounded disabled:opacity-50"
        >
          Next
        </button>
      </div>
    </div>
  );
}
